<template>
    <div class="category-wrapper">
        <div class="category-header">
            <el-button-group>
                <el-button class="action-btn" type="primary" @click="modalShow = true"><i class="el-icon-plus" />新增</el-button>
                <el-button @click="getClassifyList"><i class="el-icon-refresh-right" />刷新</el-button>
            </el-button-group>
        </div>
        <el-table
            class="category-table"
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column
                label="序号"
                type="index"
                align="center"
            >
            </el-table-column>
            <el-table-column
                prop="moduleName"
                label="模块名称"
                align="center"
            >
            </el-table-column>
            <el-table-column
                prop="modulePrice"
                label="模块值"
                align="center"
            >
            </el-table-column>
            <el-table-column
                label="操作"
                align="center"
            >
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleEdit(scope.row)">编辑</el-button>
                    <el-popconfirm title="确定删除这个分类吗?" @onConfirm="handleDel(scope.row)">
                        <el-button type="text" size="small" slot="reference">删除</el-button>
                    </el-popconfirm>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog class="dialog-w" title="新增分类" :visible="modalShow">
            <el-form :model="moduleForm" :rules="rules" ref="moduleForm" label-width="100px">
                <el-form-item label="模块名称" prop="moduleName">
                    <el-input v-model="moduleForm.moduleName" />
                </el-form-item>
                <el-form-item label="模块值" prop="modulePrice">
                    <el-input v-model="moduleForm.modulePrice" />
                </el-form-item>
                <el-form-item>
                    <el-button @click="closeModal">取消</el-button>
                    <el-button class="action-btn" type="primary" @click="submitForm">保存</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
import { get, post } from '@/api'
export default {
    data () {
        return {
            pageSize: 20,
            pageNo: 1,
            modalShow: false,
            moduleForm: {
                moduleName: "",
                modulePrice: ""
            },
            rules: {
                moduleName: [
                    {required: true, message: '请输入模块名称'}
                ],
                modulePrice: [
                    {required: true, message: '请输入模块值'}
                ]
            },
            tableData: [],
            classifyId: undefined,
        }
    },
    created () {
        this.getClassifyList()
    },
    methods: {
        getClassifyList(params){
            get('/rest/dw/BigClassifyRest/getClassifyListByPage', {
                pageSize: this.pageSize,
                pageNo: this.pageNo,
                ...params
            }).then(res => {
                const { state, rows } = res.data;
                if(state){
                    this.tableData = rows
                }
                console.log(res);
            })
        },
        handleEdit(data){
            this.classifyId = data.classifyId;
            this.moduleForm = {
                moduleName: data.moduleName,
                modulePrice: data.modulePrice
            }
            this.modalShow = true;
        },
        handleDel(data){
            post('/rest/dw/BigClassifyRest/deleteClassify', {
                classifyId: data.classifyId
            }).then(res => {
                const { state, msg } = res.data;
                if(state){
                    this.$message({
                        message: msg,
                        type: 'success'
                    });
                    this.getClassifyList();
                }
            })
        },
        closeModal(){
            this.$refs.moduleForm.resetFields();
            this.modalShow = false;
            this.classifyId = undefined;
        },
        submitForm(){
            this.$refs.moduleForm.validate((valid) => {
                if (valid) {
                    post('/rest/dw/BigClassifyRest/saveClassify', {
                        ...this.moduleForm,
                        classifyId: this.classifyId
                    }).then(res => {
                        const { state } = res.data
                        if(state){
                            this.getClassifyList();
                            this.closeModal()
                        }
                    })
                } 
            });
        }
    }
}
</script>

<style lang="scss" scoped>
.category-wrapper{
    background: #fff;
    padding: 20px;
    .category-table{
        margin-top: 20px;
    }
}
</style>