import axios from 'axios';
// import { Notification } from 'element-ui';
const currentServerHost = localStorage.getItem("currentServerHost");
// export const baseURL = currentServerHost || 'http://47.92.110.139:8081/restcloud';
export const baseURL = currentServerHost || `${window.location.origin}/restcloud`;
// export const baseURL = currentServerHost || `${window.location.origin}/ur`;

const service = axios.create({
    baseURL,
    timeout: 20 * 1000,
    responseType: "json",
}) 

service.interceptors.request.use(function (config) {
    const list = document.cookie.split("; ");
    const index = list.findIndex(o => o.includes('identitytoken'))
    // const dwIndex = list.findIndex(o => o.includes('dwToken'))
    const token = index >= 0 ? list[index].split('=')[1] : undefined;
    // const dwToken = dwIndex >= 0 ? list[dwIndex].split('=')[1] : undefined;
    if(token){
        config.headers.identitytoken = token
    }else{
        // Notification.error({title: 'token失效, 部分功能将被限制'})
    }
    // if(dwToken){
    //     config.headers.authToken = dwToken;
    // }
    return config
}, function (error) {
    return Promise.reject(error);
})

export const get = (url, params) => {
    return new Promise((resolve, reject) => {
        service.get(url, { 
            params
         }).then(res => {
            resolve(res)
        }).catch(err => {
            reject(err)
        })
    })
}

export const post = (url, params) => {
    return new Promise((resolve, reject) => {
        service.post(url, { 
            ...params
        }).then(res => {
            resolve(res)
        }).catch(err => {
            reject(err)
        })
    })
}