<template>
    <div class="maps-wrapper">
        <div class="maps-header">
            <el-button-group>
                <el-button class="action-btn" type="primary" @click="modalShow = true"><i class="el-icon-plus" />新增</el-button>
                <el-button @click="goLink"><i class="el-icon-basketball" />添加更多地图</el-button>
            </el-button-group>
            <el-row>
                <el-button icon="el-icon-refresh" circle @click="getData"></el-button>
            </el-row>
        </div>
        <el-table :data="data" stripe style="width: 100%; margin-top: 20px;">
            <el-table-column
                label="序号"
                type="index"
                align="center"
            >
            </el-table-column>
            <el-table-column
                prop="bigMapName"
                label="地图名称"
                align="center"
            >
            </el-table-column>
            <el-table-column
                prop="mapName"
                label="操作"
                align="center"
            >
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleDel(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog class="dialog-w" :visible.sync="modalShow" title="新增地图">
            <el-form :model="mapsForm" :rules="rules" ref="mapsForm" label-width="100px">
                <el-form-item label="地图名称" prop="bigMapName">
                    <el-input v-model="mapsForm.bigMapName" />
                </el-form-item>
                <el-form-item label="地图数据" prop="bigMapData">
                    <el-input type="textarea" v-model="mapsForm.bigMapData" />
                </el-form-item>
                <el-form-item>
                    <el-button @click="modalShow = false">取消</el-button>
                    <el-button class="action-btn" type="primary" @click="addMaps">保存</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import { get, post } from '@/api'
export default {
    data() {
        return {
            pageSize: 20,
            pageNo: 1,
            data: [],
            modalShow: false,
            mapsForm: {
                bigMapName: "",
                bigMapData: ""
            },
            rules: {
                bigMapName: [
                    { required: true, message: '请输入地图名称', trigger: 'blur' }
                ],
                bigMapData: [
                    { required: true, message: '请输入地图数据', trigger: 'blur' }
                ]
            },
        };
    },
    created() {
        this.getData();
    },
    methods: {
        goLink(){
            window.open('https://datav.aliyun.com/tools/atlas/index.html#&lat=30.332329214580188&lng=106.75386074913891&zoom=4.5', '_blank');
        },
        getData(){
            get('/rest/dw/BigMapRest/getBigMapListByPage', {
                pageSize: this.pageSize,
                pageNo: this.pageNo
            }).then(res => {
                const { state, rows } = res.data
                if(state){
                    this.data = rows
                }
            })
        },
        addMaps(){
            post('/rest/dw/BigMapRest/saveBigMap', {...this.mapsForm}).then(res => {
                const { state, msg } = res.data
                if(state){
                    this.$message({
                        message: msg,
                        type: 'success'
                    });
                    this.getData();
                    this.modalShow = false;
                }
            })
        },
        handleDel(row){
            console.log(row);
            post('/rest/dw/BigMapRest/deleteBigMap', {
                bigMapId: row.bigMapId
            }).then(res => {
                const { state, msg } = res.data
                if(state){
                    this.$message({
                        message: msg,
                        type: 'success'
                    });
                    this.getData();
                }
            })
        }
    },
};
</script>

<style lang="scss" scoped>
.maps-wrapper{
    background: #fff;
    padding: 20px;
    .maps-header{
        display: flex;
        justify-content: space-between;
    }
}
</style>
