<template> 
    <div class="list-wrapper">
        <el-tabs v-model="activeName" @tab-click="getBigScreenList">
            <el-tab-pane v-for="item in tabsData" :key="item.classifyId" :name="item.classifyId" :label="item.moduleName">
                <div class="bigScreenList-wrapper">
                    <div class="add-btn" @click="handleAdd">
                        <i class="el-icon-plus" />
                        <span>新建大屏</span>
                    </div>
                    <div class="bigScreenList-item" v-for="item in pageData" :key="item.bigScreenId">
                        <img class="bigScreenList-item-img" :src="item.image || defaultImg" alt="大屏缩略图" />
                        <div class="bigScreenList-item-action">
                            <el-button class="action-btn" @click="goBigScreen('view', item.bigScreenId)">预览</el-button>
                            <el-button class="action-btn" @click="goBigScreen('build', item.bigScreenId)">编辑</el-button>
                        </div>
                        <div class="bigScreenList-item-info">
                            <span>{{ item.bigScreenName }}</span>
                            <span>
                                <el-tooltip class="item" effect="dark" content="删除" placement="bottom">
                                    <el-popconfirm title="确定删除这个分类吗?" @onConfirm="handleDel(item)">
                                        <el-button type="text" slot="reference"><i class="el-icon-delete" /></el-button>
                                    </el-popconfirm>
                                </el-tooltip>
                                <el-tooltip class="item" effect="dark" content="编辑" placement="bottom">
                                    <el-button type="text" @click="handleEdit(item)" style="margin-left: 10px;"><i class="el-icon-edit-outline" /></el-button>
                                </el-tooltip>
                                <el-tooltip class="item" effect="dark" content="复制" placement="bottom">
                                    <el-button type="text" @click="handleCopy(item)"><i class="el-icon-copy-document" /></el-button>
                                </el-tooltip>
                            </span>
                        </div>
                    </div>
                </div>
            </el-tab-pane>
        </el-tabs>
        <el-dialog class="dialog-w" title="新增/编辑大屏" :visible.sync="modalShow">
            <el-form :model="bigScreenForm" :rules="rules" ref="bigScreenForm" label-width="100px">
                <el-form-item label="分类" prop="classifyId">
                    <el-select popper-class="select-bg-w" v-model="bigScreenForm.classifyId" clearable>
                        <el-option 
                            v-for="item in tabsData"
                            :key="'select-' + item.classifyId"
                            :label="item.moduleName"
                            :value="item.classifyId"
                        >
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="大屏名称" prop="bigScreenName">
                    <el-input v-model="bigScreenForm.bigScreenName" />
                </el-form-item>
                <el-form-item label="大屏尺寸">
                    <el-col :span="11"><el-input v-model="bigScreenForm.width" /></el-col>
                    <el-col :span="2" style="text-align: center; font-size: 24px"> * </el-col>
                    <el-col :span="11"><el-input v-model="bigScreenForm.height" /></el-col>
                </el-form-item>
                <el-form-item>
                    <el-button class="action-btn" type="primary" @click="submitForm">确认</el-button>
                    <el-button @click="closeModal">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
// import { getList, addObj, updateObj, delObj, getCategory, copyObj } from '@/api/visual';
import { get, post } from '@/api'
import img from '../../assets/bigScreenImg.jpg'
export default {
    name: "list",
    data() {
        return {
            defaultImg: img,
            activeName: "",
            tabsData: [],
            bigScreenForm: {
                classifyId: "",
                bigScreenId: undefined,
                bigScreenName: "",
                width: 1920,
                height: 1080
            },
            rules: {
                classifyId: [
                    {required: true, message: '请选择分类'}
                ],
                bigScreenName: [
                    {required: true, message: '请输入大屏名称'}
                ]
            },
            modalShow: false,
            pageData: []
        };
    },
    created() {
        get('/rest/dw/BigClassifyRest/getAllClassify').then(res => {
            const { state, data } = res.data
            if( state ){
                this.tabsData = data
                this.activeName = data[0].classifyId;
                this.bigScreenForm.classifyId = data[0].classifyId
                this.getBigScreenList()
            } 
        })
    },
    methods: {
        getBigScreenList(){
            get('/rest/dw/BigScreenRest/getBigScreenListByPage', {
                classifyId: this.activeName
            }).then(res => {
                const { state, rows } = res.data;
                if(state){
                    this.pageData = rows
                }
            })
        },
        handleAdd(){
            this.modalShow = true;
            this.bigScreenForm = {
                classifyId: this.activeName,
                bigScreenName: "",
                width: 1920,
                height: 1080
            }
        },
        handleEdit(data){
            this.bigScreenForm = {
                classifyId: data.classifyId,
                bigScreenId: data.bigScreenId,
                bigScreenName: data.bigScreenName,
                width: data.width,
                height: data.height
            }
            this.modalShow = true;
        },
        handleDel(data){
            post('/rest/dw/BigScreenRest/deleteBigScreen', {
                id: data.bigScreenId
            }).then(res => {
                const { state, msg } = res.data;
                if(state){
                    this.$message({
                        message: msg,
                        type: 'success'
                    });
                    this.getBigScreenList();
                }
            })
        },
        handleCopy(data){
            post('/rest/dw/BigScreenRest/getCopyBigScreenInfo', {
                id: data.bigScreenId
            }).then(res => {
                const { state, msg } = res.data;
                if(state){
                    this.$message({
                        message: msg,
                        type: 'success'
                    });
                    this.getBigScreenList();
                }
            })
        },
        closeModal(){
            this.$refs.bigScreenForm.resetFields();
            this.modalShow = false;
        },
        submitForm(){
             this.$refs.bigScreenForm.validate((valid) => {
                if (valid) {
                    const id = this.bigScreenForm.bigScreenId;
                    post('/rest/dw/BigScreenRest/saveBigScreen', {...this.bigScreenForm}).then(res => {
                        const { state, data } = res.data
                        if(state){
                            this.getBigScreenList();
                            this.closeModal();
                            if(id === undefined){
                                this.goBigScreen('build', data)
                            }
                        }
                    })
                } 
            });
        },
        goBigScreen(type, id){
            let routeUrl = this.$router.resolve({
                path: `/${type}/${id}`
            })
            window.open(routeUrl.href, '_blank');
        }
    },
};
</script>
