    width: 100%;
    height: 100%;
    padding: 20px;
    background: #fff;
    box-sizing: border-box;
    .bigScreenList-wrapper{
        display: flex;
        flex-wrap: wrap;
        .bigScreenList-item{
            position: relative;
            width: 258px;
            height: 184px;
            margin: 0 0 20px 20px;
            border: 1px solid #f0f0f0;
            &:nth-child(6n){
                margin-left: 0;
            }
            &:hover{
                .bigScreenList-item-action{
                    display: flex;
                }
            }
            .bigScreenList-item-action{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: calc(100% - 35px);
                display: none;
                align-items: center;
                justify-content: center;
                
            }
            .bigScreenList-item-img{
                width: 100%;
                height: 100%;
            }
            .bigScreenList-item-info{
                position: absolute;
                bottom: -1px;
                left: 0px;
                width: 100%;
                height: 36px;
                line-height: 36px;
                font-size: 12px;
                padding: 0 10px;
                color: #fff;
                background: rgba(0, 0, 0, .4);
                box-sizing: border-box;
                display: flex;
                justify-content: space-between;
                .el-button{
                    color: #fff;
                }
            }
        }
    }
    .add-btn{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-flow: column;
        width: 258px;
        height: 184px;
        font-size: 14px;
        border: 2px solid #f0f0f0;
        cursor: pointer;
        &:hover{
            border-color: #3b9bcd;
        }
        i{
            font-size: 18px;
        }
        span{
            margin-top: 10px;
        }
    }
}
