<template>
    <el-container class="home-wrapper">
        <el-aside
            :style="{
                width: (!isCollapse ? 200 : 80) + 'px',
                backgroundColor: '#001529',
            }"
        >
            <div class="logo" :class="isCollapse ? 'collapse' : ''">
                <img src="../assets/logo-w.png" alt="restCloud-logo" />
            </div>
            <el-menu
                :default-active="activeName"
                :collapse="isCollapse"
                background-color="#001529"
                text-color="#fff"
                active-text-color="#fff"
                @select="handleSelect"
            >
                <el-menu-item :index="1">
                    <i class="el-icon-menu"></i>
                    <span slot="title">大屏管理</span>
                </el-menu-item>
                <el-menu-item :index="2">
                    <i class="el-icon-folder-opened"></i>
                    <span slot="title">分类管理</span>
                </el-menu-item>
                <el-menu-item :index="3">
                    <i class="el-icon-location-outline"></i>
                    <span slot="title">地图管理</span>
                </el-menu-item>
            </el-menu>
        </el-aside>
        <el-container>
            <el-header class="header">
                <div class="header-left">
                    <el-button class="menu-fold" type="text" @click="isCollapse = !isCollapse">
                        <i v-if="!isCollapse" class="el-icon-s-fold"></i>
                        <i v-else class="el-icon-s-unfold"></i>
                    </el-button>
                    <span style="margin-left: 10px;color: #fff;">
                        数据大屏
                    </span>
                </div>
                <div class="header-center"></div>
                <div class="header-right">
                    <el-avatar :size="32" src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />
                    <span style="margin-left: 15px;">{{ userName }} 您好 {{ headDate }}</span>
                    <el-button class="home" type="text" icon="el-icon-s-home" @click="goHome">首页</el-button>
                    <el-button class="change-server" type="text" icon="el-icon-refresh" @click="changeServerModalShow">切换</el-button>
                    <el-button class="logout" type="text" size="small" @click="logout">
                        <span class="logout-icon">
                            <i class="el-icon-right"></i>
                        </span>
                        退出
                    </el-button>
                    <el-dialog class="dialog-w" title="切换服务器" :visible.sync="modalShow">
                        <el-form :model="serverForm" :rules="rules" ref="serverForm" label-width="120px">
                            <el-form-item label="服务器地址" prop="server">
                                <el-input v-model="serverForm.server" />
                            </el-form-item>
                            <el-form-item>
                                <el-button class="action-btn" @click="changeServer">连接服务器</el-button>
                                <el-button @click="modalShow = false">取消</el-button>
                            </el-form-item>
                        </el-form>
                    </el-dialog>
                </div>
            </el-header>
            <el-main style="background-color: #f0f2f5;">
                <list v-if="activeName === 1" />
                <category v-if="activeName === 2" />
                <maps v-if="activeName === 3" />
            </el-main>
        </el-container>
    </el-container>
</template>
<script>
import list from "./list";
import maps from "./list/map";
import category from "./list/category";
import moment from "moment";
import { baseURL } from '@/api'
export default {
    name: "index",
    components: {
        list,
        maps,
        category
    },
    data() {
        return {
            isCollapse: false,
            userName: sessionStorage.getItem("userName"),
            headDate: moment().format("MMMDo dddd"),
            activeName: 1,
            modalShow: false,
            serverForm: {
                server: baseURL
            },
            rules: {
                server: [
                    { required: true, message: '请输入服务器地址', trigger: 'change' }
                ]
            }
        };
    },
    created() {},
    methods: {
        goHome(){
            location.href = baseURL + '/admin';
        },
        logout(){
            document.cookie = "userId=; expires=Thu, 01 Jan 1970 00:00:00 GMT";
            document.cookie = "userName=; expires=Thu, 01 Jan 1970 00:00:00 GMT";
            document.cookie = "identitytoken=; expires=Thu, 01 Jan 1970 00:00:00 GMT";
            localStorage.removeItem('serverHost');
            localStorage.removeItem('currentServerHost');
            location.href = baseURL + '/admin/login';
        },
        handleSelect(key) {
            this.activeName = key;
        },
        changeServerModalShow(){
            this.modalShow = true;
            if(!this.serverForm.server){        
                this.serverForm.server = baseURL
            }
        },
        changeServer(){
            this.$router.go(0);
        }
    },
};
</script>
